import { Component, Inject, ViewChild, ElementRef } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA, MatCheckboxChange } from '@angular/material';

import * as showdown from 'showdown';

import { EmailFormAbstractComponent, parseOnlyEmails } from './email-form-abstract.component';

export interface IEmailFormDefaultInput {
    title: string;
    emailBody: string;
    emailTitle: string;
}

export interface IEmailFormDefaultOutput {
    confirmed: boolean;
    emailReceivers: string[];
    emailBody: string;
    emailTitle: string;
}

@Component({
    selector: 'polp-md-email-form',
    templateUrl: 'email-form.component.html'
})
export class EmailFormComponent extends EmailFormAbstractComponent {

    constructor(public dialogRef: MatDialogRef<EmailFormComponent>,
        @Inject(MAT_DIALOG_DATA) public data: IEmailFormDefaultInput) {
        super(dialogRef);

        data.title && (this.title = data.title);
        this.messageBody = data.emailBody || '';
    }

    public get isSubmitDisabled(): boolean {
        return this.emails.length === 0;
    }

    // Override
    public onSubmit() {
        // body
        let messageBody = this.messageBody;

        // Convert it into html
        const converter = new showdown.Converter();
        messageBody = converter.makeHtml(messageBody);

        // Prepare email list
        const emails = [];

        this.emails.forEach(elem => {
            let x = elem || (elem.value);
            const y = parseOnlyEmails(x);
            y.forEach(m => {
                emails.push(m);
            });
        });

        const outputs: IEmailFormDefaultOutput = {
            confirmed: true,
            emailReceivers: emails,
            emailBody: messageBody,
            emailTitle: this.data.emailTitle || '' // todo:
        };

        this.dialogRef.close(outputs);
    }
}
